import React from "react";
import Grid from "@material-ui/core/Grid";
import Navbar from "../Components/Navbar/Navbar";
import MobileNavbar from "../Components/MobileNavbar/MobileNavbar";

import Footer from "../Components/Footer/Footer";
import { useStyles } from "./css";

export const Host = () => {
    const classes = useStyles();
    return (
        <div className={classes.root} id="hostTop">
      <Grid container spacing={2}>
        <Grid item xs={12} sm={12} md={12} lg={12}>
          <Navbar />
          <MobileNavbar />
          <div className={classes.host}>
     <h1 className={classes.title}>HOST TERMS AND CONDITIONS</h1>
<strong style={{color:'red'}}>* 
 <b> Please read these terms and conditions carefully as they contain 
    important information.
     By posting a listing or otherwise using otopark services as a Host,
      you agree to comply with and be bound by these Host Terms and Conditions.
</b>
</strong>
<br/>
<br/>
<strong style={{color:'red'}}>* 
<b>
* Please note: These Host Terms and Conditions contain an arbitration clause and class action waiver that applies to all otopark members.
</b>
</strong>
<br/>
<br/>
The following Host Terms and Conditions apply in addition to the otopark’s terms of service, privacy policy, cancellation policy, overstay policy, payout policy and any other policies.
<br/>
<br/>
<h4>Terms</h4>
<br/>
<br/>
1.1. otopark Technological Services Host Agreement ( “us” or “otopark”) provides a website and an application connecting those seeking to book parking spaces for their vehicles (“Guest”) with our otopark registered Host who owns or operates such parking spaces (“Host”,“you” or “your”).
<br/>
<br/>
1.2. These terms and conditions will apply at the time when you use this website or application for accessing the services. By continuing to use the website or the application you accept these terms and conditions and they shall apply to the agreement between the Host and otopark.
<br/>
<br/>
1.3. This agreement governs the relationship between otopark and the Host. When you use the services we are providing you with the means to enter into individual, direct contracts with the Guest to park in your parking space. You will be bound by your obligations under this Agreement and any additional obligation included in a listing relating parking space. We are not a party to the agreement between you and the Guest and we will not be liable to you, the Guest or any third party for any breach of obligations in relation to the parking space or otherwise. We simply facilitate the payment between you and the Guest for the use of the parking space provided by you.
<br/>
<br/>
1.4. In order to register an account, you are required to provide certain information, which includes, but is not limited to, name, phone number, email id, your account details to enable us to make payments to you. If you do not provide us with these details you will not be able to use our services.
<br/>
<br/>
1.5. This terms and conditions may be amended from time to time as we continually develop the services we provide. We may amend this agreement by posting the amended terms and conditions on our website www.otopark.in . These amended terms and conditions would take effect in 10 days following being posted. If you no longer wish to use the services, you can simply stop using the services. You can also log out from your account with us and can contact us by emailing info@ototpark.in and request that we deactivate your account. Any amendments or new terms and conditions will be made available on our website. The terms and conditions on the website at the time you enter into booking for a parking space or use any other services will be the ones that apply. You may terminate this agreement if you do not wish to be bound by any such amendments but by continuing to use our website or services you will be deemed to have accepted the new terms.
<br/>
<br/>
1.6. This agreement is with the Host, the person using this services. You will comply with the terms of this agreement and any additional requirements in relation to the parking space. If you allow another person to assist with managing a booking or parking space you agree you will remain responsible for the booking of parking space.
<br/>
<br/>
<h4>Services and listing</h4>
<br/>
<br/>
2.1. In order to take Bookings for your Parking Spaces you must register an Account as set out in www.otopark.in or in the otopark application.
<br/>
<br/>
2.2. We may advertise your parking space on our website and otopark application and provide information to the Guest and prospective Guest who may wish to use the parking space.
<br/>
<br/>
2.3. You will be required to complete your own listing for the website or mobile application. In your listing you agree to provide all relevant information about the parking space including:
<br/>
<br/>
a. The address and a full description of the Parking Space;
<br/>
<br/>
b. Time duration for which your parking space is available;
<br/>
<br/>
c. Any restrictions on the types of vehicles that are suitable for the Parking Space;
<br/>
<br/>
d. Any other information or restrictions which apply to the Parking Space which the Guest should be aware of before booking the Parking Space.
<br/>
<br/>
2.4. You are also required to provide us separately with information before we will list the Parking Space including your name, address and telephone number
<br/>
<br/>
2.5. You must provide us with any other information relating to you and the Parking Space as we may reasonably request at any time and we may also ask you to clarify any information you have provided. This may include, but is not limited to, your aadhar card details and/or any contracts relating to your rent or ownership of the Parking Space.
<br/>
<br/>
2.6. You are responsible for the accuracy of all information in your listing and any information you provide to us relating to a Booking. You may be liable to the Guest if the information you provide is inaccurate, incomplete or misleading in any way.
<br/>
<br/>
2.7. We reserve the right to conduct a physical inspection of the Parking Space on reasonable prior notice and may remove your listing if the Parking Space does not conform to the information you have provided to our standards, or if we believe you to be in breach of your obligation.
<br/>
<br/>
2.8. In addition to advertising the Parking Space, we may be entitled to send promotional emails to the Guests and provide such information about your Parking Space as we may deem appropriate to promote otopark or your Parking Space.
<br/>
<br/>
2.9. We may refer to you or your Parking Space at any time in the future to publicise ourselves or our website. To the extent a Parking Space relates to a residential address we will not reveal the specific address in such publicity.
<br/>
<br/>
<h4>Relationship between otopark and the Host</h4>
<br/>
<br/>
3.1. You appoint us as your platform provider for the purposes of forming binding agreements between you and the Guest in relation to the Bookings. You grant a license to the Guest to use the parking space to which a booking relates for the period of that booking. You also appoint us to collect your booking fees in relation to the booking from the Guest.
<br/>
<br/>
3.2. At the time the Booking of the Parking Space is confirmed and payment is received from the Guest, a binding agreement will be formed between you and the Guest. The agreement between you and the Guest will include the relevant terms and conditions of these terms and conditions and will also include any additional restrictions relating to your Parking Space if listed by you.
<br/>
<br/>
3.3. You may not incorporate any additional terms into the agreement between you and the driver other than the restrictions clearly included in your listing.
<br/>
<br/>
<h4>Bookings and Payments</h4>
<br/>
<br/>
4.1. otopark allows you to list your parking space on the platform along with the booking period. The Guest can select desired parking space and a booking request will be sent to the respective Host. The Host is required to confirm the booking in order to accept payment from the Guest. The Host may also set their account on Auto-approval mode by which the booking will be confirmed automatically.
<br/>
<br/>
4.2. If the booking is accepted by you, we will notify the Guest and we will provide the Guest with the contact details you have supplied to us without further reference to you, and we will take payment from the Guest on your behalf.
<br/>
<br/>
4.3. Once the necessary payment from the Guest has been received, you will have entered into a binding agreement with the Guest to occupy the parking space during the booking period (set out in the listing on the platform).
<br/>
<br/>
4.4. The parking space and license for the booking period: Before any booking period you must ensure that the Parking Space is in a satisfactory condition, complies with the information given in your listing and is available for the duration of the Booking period.
<br/>
<br/>
4.5. Unless you have agreed to the Guest being given a later departure time or further Booking Period, if the Guest stays longer than the Booking Period, the Guest will be liable to pay the full price for any additional time stayed. If the Guest does stay for such an additional period you authorise otopark to collect payment of any such additional fees from the Guest on your behalf.
<br/>
<br/>
4.6. You represent and confirm that you own the Parking Space or are authorized to allow third party to use parking space and that the license for the Guest to use the Parking Space during the Booking Period will comply with all applicable laws, tax requirements and law governing rental of or licenses to use residential and other properties.
<br/>
<br/>
4.7. You agree to notify us immediately upon receiving any notice or correspondence from the government authority, landlord or any housing society (or other persons who controls the parking space) in connection with your use of the Parking Space for the Services. Following such notice we reserve the right to terminate this Agreement and remove the Parking Space from the Platform.
<br/>
<br/>
4.8. We will not be liable to you, the Guest or any other third party if you do not have the necessary authority referred to in clause 4.6.
<br/>
<br/>
4.9. Payout: The payout will be made to Host depending upon the nature of the booking the Host had (hourly/ daily/ weekly/ monthly). There is a layover time between the release of the funds and having them arrive in your account.
<br/>
<br/>
4.10. The Host will be receiving payout of accumulated shared park earning on a monthly, usually on the 5th day of every month (might get delayed in case if it a weekend, government holiday or national holiday) for sharing their parking space.
<br/>
<br/>
4.11. The Host may change, add or remove the otopark payout methods by going to the profile area of their account.
<br/>
<br/>
4.12. otopark is authorised to levy a 30% commission on the accumulated shared parking earnings made by the respective Host.
<br/>
<br/>
<h4>Cancellation</h4>
<br/>
<br/>
5.1. If you want to cancel a Booking you must do so through the cancellation policy, which may be updated from time to time. To cancel a Booking you must log into your Account and process the cancellation through the Platform in accordance with the Cancellation Policy. Cancellation of a Booking will take effect at the point you receive an acknowledgement of such cancellation from otopark.
<br/>
<br/>
5.2. You agree that if a Guest wishes to cancel a booking they may do so through us and our Platform. If we are required to process a cancellation we will do so in accordance with the Cancellation Policy.
<br/>
<br/>
5.3. If we have received any payments in respect of a booking before a booking period begins and such booking is subsequently cancelled by you or the Guest, you authorise us to refund the Guest from the payment we are holding on your behalf. Where you have received payments you are required to refund the Guest in accordance with the Cancellation Policy.
<br/>
<br/>
5.4. We reserve the right to cancel your account and terminate this agreement if you do not refund a Guest within the time frames specified in the Cancellation Policy. You agree that you will be responsible for such refunds and that we may use any funds we are holding on your behalf in order to refund a Guest or otopark may settle the refund owed to the Guest directly with the Guest and then recover this money from you afterwards.
<br/>
<br/>
<h4>Acknowledgments and Agreements by the Host</h4>
<br/>
<br/>
6.1. You acknowledge and agree that you must:
<br/>
<br/>
i. honor all the booking with the guest and ensure that the Parking Spaces is available for the duration of the Booking Period;
<br/>
<br/>
ii. not obstruct the Guest or prevent the Guest from parking in the Parking Space and not allow any vehicle, other than the vehicle detailed in the Booking to use the parking Spaces during the Booking Period;
<br/>
<br/>
iii. not deliberately do, or omit to do, anything which will or is likely to put the Guest or their vehicle at risk;
<br/>
<br/>
iv. provide your Parking Space in accordance with the details and information set out in your listing;
<br/>
<br/>
v. ensure that all the information about you and your Parking Space that you provide to us for inclusion on the Website is true and accurate in all respects and could not be construed in any way as misleading to a Guest;
<br/>
<br/>
vi. ensure that the Parking space is clean, tidy and clear of rubbish at the start of the Booking Period;
<br/>
<br/>
vii. deal with all Guest in a professional and courteous manner and in such a way as to not cause any harm or damage to our reputation;
<br/>
<br/>
viii. deal with all queries from Guest relating to a Parking Space or booking in a prompt and satisfactory manner;
<br/>
<br/>
ix. be available by telephone, SMS or email prior to or at the commencement of the Booking Period to direct the Guest to the Parking Space;
<br/>
<br/>
6.2. You agree that you will not create any false account with otopark or use your account with otopark for any immoral or illegal activity or purpose including malicious or fraudulent bookings or money laundering.
<br/>
<br/>
6.3. You agree to use otopark as your exclusive platform provider for the purposes of making and accepting bookings for your parking space from introduced guests.
<br/>
<br/>
<h4>Termination and suspension</h4>
<br/>
<br/>
7.1. If you do not comply with the terms of this Agreement, we may suspend or close your account and you will not be able to use our Services including taking and accepting Bookings.
<br/>
<br/>
7.2. If we suspend your account or access to the Website and Application for any reason, we may refuse to provide you with any services including the right to take or accept any further Bookings. If you attempt to avoid this by creating or attempting to create a new account, we reserve the right to terminate this Agreement and any existing Account you may have.
<br/>
<br/>
7.3. If we suspend or terminate your Account, upon termination you will no longer be able to use our Services or take or accept Bookings through us. If you have any outstanding Bookings for which Guest have made payments directly to you, you must refund those Guest for those Bookings in accordance with the Cancellation Policy and Bookings cannot proceed. The terms of this Agreement will continue to the extent they relate to existing consequences of previous Booking.
<br/>
<br/>
7.4. You may also terminate this Agreement at any time, but if you terminate this Agreement you agree to honor any outstanding Bookings at the time of termination. From the date of termination, we will not confirm or accept any new bookings for the Parking Space but the Agreement will continue in respect of those outstanding Bookings.
<br/>
<br/>
<h4>Disclaimers and Limitations of Liability</h4>
<br/>
<br/>
8.1. If you choose to use otopark Website and Services, as a Host, you do so at your sole risk. You acknowledge and agree that otopark does not have any obligation to conduct background checks on any Guest. The Website and Services are provided “as is” without warranty of any kind, either express or implied. otopark makes no promises that the Website or the services will meet your requirements or available on an uninterrupted, secure, or error-free basis. otopark makes no promises regarding the quality of the services or the accuracy, timeliness, truthfulness, completeness or reliability of any content obtained through the Website or Services.
<br/>
<br/>
8.2. You are solely responsible for all your communications and interactions with other users of the Website or Services and with other persons with whom you communicate or interact as a result of your use of the Website or Services, including, but not limited to, any Guest. You understand that otopark does not make any attempt to verify the statements of users of the Website or Services. To agree to take reasonable precautions in all communications and interactions with other users of the Website or Services, including, but not limited to Guests, particularly if you decide to meet in person.
<br/>
<br/>
8.3. We accept liability for fraud and fraudulent misrepresentation by us.
<br/>
<br/>
8.4. Other than our liability mentioned above, if we breach these terms or are otherwise liable to you for any other type of claim, we shall only be liable to you for losses up to the difference between the amount paid by the Guest in relation to the booking giving rise to the dispute between us.
<br/>
<br/>
8.5. We are not responsible for losses not caused by our breach, indirect losses, any loss arising as a result of the accuracy, timeliness, completeness or usefulness of any information; or, failure to provide the services or to meet any of our obligations under these terms where such failure is due to events beyond our control.
<br/>
<br/>
8.6. Nothing in these terms is intended to or will affect these statutory rights. You agree that the above exclusions of liability are reasonable in all the circumstances, especially in light of the fact that our Services include only provision of the Website and Services and responsibility for the payment of the fees in relation to a Parking Space lies solely with the Guest.
<br/>
<br/>
<h4>Dispute resolution</h4>
<br/>
<br/>
9.1. You agree that if you have any dispute with a Guest concerning your Parking Space or any use of the Parking space you will attempt to resolve it amicably in the first instance by directly communicating with the Guest.
<br/>
<br/>
9.2. In the event that a dispute cannot be resolved with the Guest directly, you may refer the dispute to us or make a complaint. Similarly, A Guest may refer a dispute to us. We are under no obligation to resolve any dispute between you and the Guest. However, we may seek to resolve the dispute between you and the Guest and we may require that the Guest makes further payment to you or we may require that you refund payments you have received from the relevant Booking.
<br/>
<br/>
9.3. You agree that if we determine that you should make a refund or other payment to a Guest, you will be responsible for these amounts and we may use any funds we are holding on your behalf in order to settle a dispute, otopark may settle the amount owed to the Guest directly with the Guest and then seek the funds from you.
<br/>
<br/>
9.4. You agree that the dispute between you and otopark will be resolved by binding Arbitration. The arbitration shall be carried out in accordance with the provisions of the Arbitration and Conciliation Act, 1996 as amended. The language of the arbitration shall be English and the seat and venue of arbitration shall be in New Delhi.
<br/>
<br/>
9.5. You further agree that in the event that the process of the courts is required to be invoked for enforcement of this clause, the competent courts at New Delhi alone shall have exclusive jurisdiction.
<br/>
<br/>
9.6. You wave your right to participate in a class action lawsuit.
<br/>
<br/>
9.7. You agree that the provisions in this clause will survive any termination of this Agreement.
<br/>
<br/>
<h4>Insurance</h4>
<br/>
<br/>
You will be entirely responsible for any and all insurance that you may require for the purposes of granting any license to use your parking space.
<br/>
<br/>
<h4>Confidentiality</h4>
<br/>
<br/>
Except as otherwise agreed in these terms and conditions, each party agrees with the other to keep secret and not share any confidential information it receives from the other party through the services.
<br/>
<br/>
<h4>Indemnification</h4>
<br/>
<br/>
You agree to release, defend, indemnify, and hold otopark and its affiliates, and its officers, directors, employees and agents, harmless from and against any claims, liability, damages, losses, and expenses, including, without limitation, reasonable legal and accounting fees, arising out of or in any way connected with the otopark Host Terms. Further, You agree to hold us harmless against any claims made by any Guest or any Third Party due to, or arising out of, or in connection with Your use of the platform, any claim that Your material/content caused damage to a Guest or a Third Party, Your violation of the Host Terms, or Your violation of any rights of another, including any intellectual property rights.
<br/>
<br/>
<h4>General Provisions</h4>
<br/>
<br/>
13.1. You acknowledge that we may transfer our rights under the terms to any third party without having to obtain your prior permission provided that this will not affect the standard of service you receive under this agreement and that we notify you of the date on which we will transfer our rights and obligations under this Agreement to another legal entity, your only rights under or in connection with this Agreement will be against the new legal entity and not against us.
<br/>
<br/>
13.2. You agree that these terms are fair and reasonable in all the circumstances. However, if any part of these terms is disallowed or found to be ineffective by a court or regulator, the other provisions shall continue to apply.
<br/>
<br/>
13.3. If you breach these terms and we take no action against you, we will be entitled to use our rights and remedies in any other situation where you breach these terms.
<br/>
<br/>
13.4. You agree that your right to access and use the Services is also subject to applicable law and any rules or policies applied by the relevant App Store from which you access the App.  


          </div>
        <Footer />
        </Grid>
      </Grid>
    </div>
    )
}
export default Host
